body{
    color: #4a4a4a;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    font-family: BlinkMacSystemFont, -apple-system, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", "Helvetica", "Arial", sans-serif;}
.title:not(:last-child),.subtitle:not(:last-child){margin-bottom:1.5rem;}
h1,h3,body{margin:0;padding:0;}
h1,h3{font-size:100%;font-weight:normal;}
section{display:block;}
.container{margin:0 auto;position:relative;max-width: 590px;}
@media (max-width: 400px) {
    .container {max-width: 100%;}
    #avz > div, #avz > a, #avz > button {max-width: 100%;text-overflow: ellipsis;direction: rtl;}
    #avz #gloginbtn > div {max-width: 100%;text-overflow: ellipsis;direction: rtl;}
}
.title,.subtitle{word-break:break-word;}
.title{color:#363636;font-size:2rem;font-weight:600;line-height:1.125;}
.title:not(.is-spaced) + .subtitle{margin-top:-1.25rem;}
.subtitle{color:#4a4a4a;font-size:1.25rem;font-weight:400;line-height:1.25;}
.panel{font-size:1rem;}
.hero{align-items:stretch;display:flex;flex-direction:column;justify-content:space-between;}
.hero.is-fullheight .hero-body{align-items:center;display:flex;}
.hero.is-fullheight .hero-body > .container{flex-grow:1;flex-shrink:1;}
.hero.is-fullheight{min-height:100vh;}
.hero-body{flex-grow:1;flex-shrink:0;padding:3rem 1.5rem;}

