.clouds {
    background-repeat: repeat;
    background-position: 0 0;
    /*-webkit-animation: 30s linear 0s normal none infinite clouds;
    -moz-animation: 30s linear 0s normal none infinite clouds;
    -ms-animation: 30s linear 0s normal none infinite clouds;
    -o-animation: 30s linear 0s normal none infinite clouds;
