#avz {
    padding: 0 30px 1rem 30px;

}
#avz::after {
    content: "";
    clear: both;
    display: table;
}
#avz > a, #avz > div, #avz > button {
    display: inline-block;
    margin-right: 10px;
    width: 240px;
    height: 50px;
}

#avz > a, #avz > button {
    line-height: 50px;
    text-indent: 33px;
    font-weight: 600;
    font-size: 16px;
    border-radius: 2px;
    -webkit-border-radius: 2px;
    color: #757575;
    font-family: Helvetica, Arial, sasn-serif;
    letter-spacing: .25px;
    overflow: hidden;
    text-align: center;
    text-overflow: clip;
    white-space: nowrap;
    text-decoration: none;
    transition: box-shadow 300ms;
    -webkit-box-shadow: 0 2px 4px 0px rgba(0,0,0,.25);
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.25);
    -webkit-transition: background-color .218s,border-color .218s,box-shadow .218s;
    transition: background-color .218s,border-color .218s,box-shadow .218s;
    position: relative;
}
#avz > a:hover, #avz > button:hover {
    -webkit-box-shadow: 0 0 3px 3px rgba(66,133,244,.3);
    box-shadow: 0 0 3px 3px rgba(66,133,244,.3);
}

#avz > button {
    border: none;
    margin: 0;
    vertical-align: top;
    cursor: pointer;
    padding-left: 0 !important;
    padding-right: 0 !important;
    background: #fff;
}
/* icon */
#avz > a::before, #avz > button::before {
    position: absolute;
    background-color: #fff !important;
    display: block;
    width: 50px;
    height: 50px;
    content: '';
    background: no-repeat left;    
    background-position: 15px 12px;
    left: 0;
}
#avz #gloginbtn .abcRioButtonIcon {
    padding: 15px;
    background: #fff;
    z-index: 99;
    position: absolute;
    left: 0;
}
#avz #gloginbtn .abcRioButtonLightBlue:active {
    background: #fff !important;
}
#avz #emailbtn::before {
    background-image: url('data:image/png;base64,');
}

#avz #loginfb::before {
    background-image: url('data:image/png;base64,');
}
#avz #loginfb:hover {
    -webkit-box-shadow: 0 0 5px 0 rgba(9,30,66,0.3);
    box-shadow: 0 0 5px 0 rgba(9,30,66,0.3);
}

#avz #github_btn::before {
    background-image: url('data:image/png;base64,');
    background-size: 24px;
    background-position: 15px 13px;
}

#avz #twlogin::before {
    background-image: url('data:image/png;base64,');
}

#avz #microsoftbtn::before {
    background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABUAAAAVCAYAAACpF6WWAAAACXBIWXMAAAsSAAALEgHS3X78AAAAVElEQVQ4jWP8//8/A7UBC8i8z4HKBE3mXX+XsWE3I0F1Da7/GZmo7kwGBoZRQ0cNpTKgXY5iXPqBoMn/owUYGXYRzlEMbqM5atTQoWEo9XMUAwMDAGSOGCOVddy/AAAAAElFTkSuQmCC');
